// React core
import React from 'react'
// import PropTypes from 'prop-types'

// Styling
import { styled } from '@material-ui/styles'

// Components
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import Avatar from '@material-ui/core/Avatar'
import Snackbar from '@material-ui/core/Snackbar'
import SnackbarContent from '@material-ui/core/SnackbarContent'
import CheckCircleIcon from '@material-ui/icons/CheckCircle'
import Fab from '@material-ui/core/Fab'
import AddIcon from '@material-ui/icons/Add'
import TextField from '@material-ui/core/TextField'
import green from '@material-ui/core/colors/green'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import Slide from '@material-ui/core/Slide'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import AppBar from '@material-ui/core/AppBar'

const Root = styled('section')({ padding: 8 })

const PaperStyled = styled(Paper)({
  padding: '1rem'
})

const WelcomeMessage = styled(Typography)({
  color: 'rgba(0, 0, 0, 0.67)'
})

const JobContainer = styled(Paper)({
  padding: '1rem',
  height: 520,
})

const JobWrapper = styled('div')({
  height: '100%',
  overflowY: 'auto'
})

const ListItemTextMobile = styled(ListItemText)(theme => ({
  [theme.breakpoints.down('xs')]: {
    display: 'none'
  }
}))

const DialogAppBar = styled(AppBar)({
  position: 'relative'
})

const DialogContainer = styled('section')({
  width: '100%',
  height: '100%',
  padding: '1rem'
})

const TextFieldStyled = styled(TextField)({
  width: '100%'
})

const GridStyled = styled(Grid)(theme => ({
  paddingRight: theme.spacing.unit / 2,
  paddingLeft: theme.spacing.unit / 2
}))

const Spacer = styled('div')({ height: 16 })

const Transition = props => <Slide direction='up' {...props} />

class Job extends React.Component {
  static propTypes = {}

  state = {
    anchorEl: null,
    dialogOpened: false,
    snackOpened: false,
    adding: false
  }

  componentWillUnmount () {
    clearTimeout(this.handleMenuClose)
  }

  handleMenuOpen = event => {
    this.setState({ anchorEl: event.currentTarget })
  }

  handleMenuClose = () => setTimeout(() => {
    this.setState({ anchorEl: null })
  }, 200)

  handleDialogOpenToAdd = () => {
    this.setState({ adding: true, dialogOpened: true })
  }

  handleDialogCloseToAdd = () => {
    this.setState({ adding: false, dialogOpened: false })
  }

  handleDialogOpen = () => {
    this.setState({ dialogOpened: true })
    this.handleMenuClose()
  }

  handleDialogClose = () => {
    this.setState({ dialogOpened: false })
  }

  handleSnackOpen = () => {
    this.setState({ snackOpened: true })
  }

  handleSnackClose = () => {
    this.setState({ snackOpened: false })
  }

  render () {
    return (
      <Root>
        <Grid container spacing={16}>
          <Grid item xs={12}>
            <PaperStyled>
              <div style={{ padding: 4 }}>
                <Grid container spacing={8}>
                  <Grid item xs={11}>
                    <Typography variant='h6'>
                      Videoaulas
                    </Typography>
                    <WelcomeMessage variant='body1'>
                      Aqui estão listadas todas as videoaulas do sistema!
                    </WelcomeMessage>
                  </Grid>
                  <Grid item xs={1}>
                    <Fab color='primary' onClick={this.handleDialogOpenToAdd} aria-label='Add'>
                      <AddIcon />
                    </Fab>
                  </Grid>
                </Grid>
              </div>
            </PaperStyled>
          </Grid>
          <Grid item xs={12}>
            <JobContainer>
              <JobWrapper>
                <List>
                  <ListItem
                    button
                    aria-haspopup='true'
                    aria-controls='lock-menu'
                    aria-label='Opções'
                    onClick={this.handleMenuOpen}
                  >
                    <Avatar src='https://i.imgur.com/5wgWi32.jpg' />
                    <ListItemText primary='Microsoft Word' secondary='Não há expiração' />
                    <ListItemTextMobile secondary='A video aula foi produzida pelo projeto de extensão realizado pelo Prof. Dr. Fernando Ernesto...' />
                  </ListItem>
                </List>
              </JobWrapper>
            </JobContainer>
          </Grid>
        </Grid>
        <Dialog
          fullScreen
          open={this.state.dialogOpened}
          onClose={this.handleDialogClose}
          TransitionComponent={Transition}
        >
          <DialogAppBar position='fixed'>
            <Toolbar>
              <IconButton color='inherit' onClick={this.state.adding ? this.handleDialogCloseToAdd : this.handleDialogClose} aria-label='Close'>
                <CloseIcon />
              </IconButton>
              <Typography variant='h6' color='inherit' style={{ flex: 1 }}>
                {this.state.adding ? 'Adicionar' : 'Editar'}
              </Typography>
              <Button color='inherit' onClick={() => { this.state.adding ? this.handleDialogCloseToAdd() : this.handleDialogClose(); this.handleSnackOpen(); }}>
                salvar
              </Button>
            </Toolbar>
          </DialogAppBar>
          <DialogContainer>
            <PaperStyled>
              <Typography variant='h6'>
                Informações da videoaula
              </Typography>
              <WelcomeMessage variant='body1'>
                {this.state.adding ? 'Preencha os campos da nova videoaula!' : 'Para editá-las, basta alterar as informações e confirmar no botão "Salvar".'}
              </WelcomeMessage>
            </PaperStyled>
            <Spacer />
            <PaperStyled>
              <Typography variant='h6'>
                Título e expiração
              </Typography>
              <WelcomeMessage variant='body1'>
                O título da videoaula é o detalhe mais importante do cadastro, é ele quem será lido ao aluno através do sistema de voz na listagem das videoaulas.
              </WelcomeMessage>
              <div style={{ padding: 8 }}>
                <Grid container spacing={16}>
                  <GridStyled item xs={12}>
                    <TextFieldStyled
                      label='Título'
                      type='text'
                      margin='normal'
                      variant='outlined'
                      defaultValue={this.state.adding ? '' : 'Microsoft Word'}
                    />
                  </GridStyled>
                </Grid>
              </div>
            </PaperStyled>
            <Spacer />
            <PaperStyled>
              <Typography variant='h6'>
                Fonte do vídeo
              </Typography>
              <WelcomeMessage variant='body1'>
                A fonte do vídeo (URL) é onde o vídeo está armazenado, cole no campo abaixo:
              </WelcomeMessage>
              <div style={{ padding: 8 }}>
                <Grid container spacing={16}>
                  <GridStyled item xs={12}>
                    <TextFieldStyled
                      label='Fonte do vídeo'
                      type='text'
                      margin='normal'
                      variant='outlined'
                      defaultValue={this.state.adding ? '' : 'https://www.youtube.com/watch?v=a5kTAqP39jI'}
                    />
                  </GridStyled>
                </Grid>
              </div>
            </PaperStyled>
            <Spacer />
            <PaperStyled>
              <Typography variant='h6'>
                Descrição
              </Typography>
              <WelcomeMessage variant='body1'>
                A descrição deve fornecer informações adicionais sobre a videoaula.
              </WelcomeMessage>
              <Spacer />
              <textarea
                defaultValue={this.state.adding ? '' : 'A video aula foi produzida pelo projeto de extensão realizado pelo Prof. Dr. Fernando Ernesto Kintschner em colaboração com o CIAPD.'}
                placeholder='Descrição da videoaula'
                style={{
                  width: '100%',
                  height: 166,
                  resize: 'none'
                }}
              />
            </PaperStyled>
          </DialogContainer>
        </Dialog>
        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={this.state.snackOpened}
          autoHideDuration={6000}
          onClose={this.handleSnackClose}
        >
          <SnackbarContent
            style={{ backgroundColor: green[600] }}
            aria-describedby='Sucesso!'
            message={
              <span
                id='client-snackbar'
                style={{
                  display: 'flex',
                  alignItems: 'center'
                }}
              >
                <CheckCircleIcon
                  style={{
                    fontSize: 20,
                    opacity: 0.9,
                    marginRight: 14
                  }}
                />
                Videoaula salva com sucesso!
              </span>
            }
            action={[
              <IconButton
                key='close'
                aria-label='Close'
                color='inherit'
                onClick={this.handleSnackClose}
              >
                <CloseIcon style={{ fontSize: 20 }} />
              </IconButton>
            ]}
          />
        </Snackbar>
        <Menu
          id='lock-menu'
          anchorEl={this.state.anchorEl}
          open={Boolean(this.state.anchorEl)}
          onClose={this.handleMenuClose}
        >
          <MenuItem onClick={this.handleDialogOpen}> Editar videoaula </MenuItem>
          <MenuItem onClick={() => { this.handleMenuClose(); }}> Excluir videoaula </MenuItem>
        </Menu>
      </Root>
    )
  }
}

export default Job
