// React core
import React from 'react'
// import PropTypes from 'prop-types'

// Styling
import { styled } from '@material-ui/styles'

// Components
import Grid from '@material-ui/core/Grid'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import Avatar from '@material-ui/core/Avatar'
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import Fab from '@material-ui/core/Fab'
import AddIcon from '@material-ui/icons/Add'
import Slide from '@material-ui/core/Slide'
import TextField from '@material-ui/core/TextField'
import Snackbar from '@material-ui/core/Snackbar'
import SnackbarContent from '@material-ui/core/SnackbarContent'
import CheckCircleIcon from '@material-ui/icons/CheckCircle'
import green from '@material-ui/core/colors/green'

const Root = styled('section')({ padding: 8 })

const PaperStyled = styled(Paper)({
  padding: '1rem'
})

const CurriculumContainer = styled(Paper)({
  padding: '1rem',
  height: 520,
})

const CurriculumWrapper = styled('div')({
  height: '100%',
  overflowY: 'auto'
})

const WelcomeMessage = styled(Typography)({
  color: 'rgba(0, 0, 0, 0.67)'
})

const DialogAppBar = styled(AppBar)({
  position: 'relative'
})

const DialogContainer = styled('section')({
  width: '100%',
  height: '100%',
  padding: '1rem'
})

const TextFieldStyled = styled(TextField)({
  width: '100%'
})

const GridStyled = styled(Grid)(theme => ({
  paddingRight: theme.spacing.unit / 2,
  paddingLeft: theme.spacing.unit / 2
}))

const Transition = props => <Slide direction='up' {...props} />

class Curriculum extends React.Component {
  static propTypes = {}

  state = {
    anchorEl: null,
    dialogOpened: false,
    snackOpened: false,
    adding: false,
    users: [
      {
        avatar: 'https://i.imgur.com/6b4ULQJ.png',
        name: 'José Carlos de Oliveira',
        email: 'josecarlos@gmail.com'
      },
      {
        avatar: 'https://i.imgur.com/vwImtyQ.jpg',
        name: 'Guilherme Guimarães',
        email: 'guiguimaraes@vatican.com'
      }
    ]
  }

  componentWillUnmount () {
    clearTimeout(this.handleMenuClose)
  }

  handleMenuOpen = event => {
    this.setState({ anchorEl: event.currentTarget })
  }

  handleMenuClose = () => setTimeout(() => {
    this.setState({ anchorEl: null })
  }, 200)

  handleDialogOpen = () => {
    this.setState({ dialogOpened: true })
    this.handleMenuClose()
  }

  handleDialogClose = () => {
    this.setState({ dialogOpened: false })
  }

  handleSnackOpen = () => {
    this.setState({ snackOpened: true })
  }

  handleSnackClose = () => {
    this.setState({ snackOpened: false })
  }

  handleDialogOpenToAdd = () => {
    this.setState({ adding: true, dialogOpened: true })
  }

  handleDialogCloseToAdd = () => {
    this.setState({ adding: false, dialogOpened: false })
  }

  render () {
    return (
      <Root>
        <Grid container spacing={16}>
          <Grid item xs={12}>
            <PaperStyled>
              <div style={{ padding: 4 }}>
                <Grid container spacing={8}>
                  <Grid item xs={11}>
                    <Typography variant='h6'>
                      Administradores
                    </Typography>
                    <WelcomeMessage variant='body1'>
                      Todos os administradores do sistema estão cadastrados aqui!
                    </WelcomeMessage>
                  </Grid>
                  <Grid item xs={1}>
                    <Fab color='primary' onClick={this.handleDialogOpenToAdd} aria-label='Add'>
                      <AddIcon />
                    </Fab>
                  </Grid>
                </Grid>
              </div>
            </PaperStyled>
          </Grid>
          <Grid item xs={12}>
            <CurriculumContainer>
              <CurriculumWrapper>
                <List>
                  {this.state.users.map((elem, i) =>
                    <ListItem
                      key={i}
                      button
                      aria-haspopup='true'
                      aria-controls='lock-menu'
                      aria-label='Opções'
                      onClick={this.handleMenuOpen}
                    >
                      <Avatar src={elem.avatar} />
                      <ListItemText primary={elem.name} secondary={elem.email} />
                    </ListItem>
                  )}
                </List>
              </CurriculumWrapper>
            </CurriculumContainer>
          </Grid>
          <Dialog
            fullScreen
            open={this.state.dialogOpened}
            onClose={this.handleDialogClose}
            TransitionComponent={Transition}
          >
            <DialogAppBar position='fixed'>
              <Toolbar>
                <IconButton color='inherit' onClick={this.state.adding ? this.handleDialogCloseToAdd : this.handleDialogClose} aria-label='Close'>
                  <CloseIcon />
                </IconButton>
                <Typography variant='h6' color='inherit' style={{ flex: 1 }}>
                  {this.state.adding ? 'Adicionar' : 'Editar'}
                </Typography>
                <Button color='inherit' onClick={() => { this.state.adding ? this.handleDialogCloseToAdd() : this.handleDialogClose(); this.handleSnackOpen(); }}>
                  salvar
              </Button>
              </Toolbar>
            </DialogAppBar>
            <DialogContainer>
              <PaperStyled>
                <Typography variant='h6'>
                  Informações pessoais
              </Typography>
                <WelcomeMessage variant='body1'>
                  {this.state.adding ? 'Preencha os campos do novo administrador!' : 'Para editá-las, basta alterar as informações e confirmar no botão "Salvar".'}
                </WelcomeMessage>
                <div style={{ padding: 8 }}>
                  <Grid container spacing={16}>
                    <GridStyled item xs={12} sm={6} md={4} lg={3}>
                      <TextFieldStyled
                        label='Nome'
                        type='text'
                        margin='normal'
                        variant='outlined'
                        defaultValue={this.state.adding ? '' : 'José Carlos de Oliveira'}
                      />
                    </GridStyled>
                    <GridStyled item xs={12} sm={6} md={4} lg={3}>
                      <TextFieldStyled
                        label='E-mail'
                        type='email'
                        margin='normal'
                        variant='outlined'
                        defaultValue={this.state.adding ? '' : 'josecarlos@outlook.com'}
                      />
                    </GridStyled>
                    <GridStyled item xs={12} sm={6} md={4} lg={3}>
                      <TextFieldStyled
                        label='Senha'
                        type='password'
                        margin='normal'
                        variant='outlined'
                        defaultValue=''
                      />
                    </GridStyled>
                    <GridStyled item xs={12} sm={6} md={4} lg={3}>
                      <TextFieldStyled
                        label='RF'
                        type='text'
                        margin='normal'
                        variant='outlined'
                        defaultValue={this.state.adding ? '' : '18002847'}
                      />
                    </GridStyled>
                  </Grid>
                </div>
              </PaperStyled>
            </DialogContainer>
          </Dialog>
        </Grid>
        <Snackbar
          anchorOrigin={{
            vertical: 'bottom',
            horizontal: 'left',
          }}
          open={this.state.snackOpened}
          autoHideDuration={6000}
          onClose={this.handleSnackClose}
        >
          <SnackbarContent
            style={{ backgroundColor: green[600] }}
            aria-describedby='Sucesso!'
            message={
              <span
                id='client-snackbar'
                style={{
                  display: 'flex',
                  alignItems: 'center'
                }}
              >
                <CheckCircleIcon
                  style={{
                    fontSize: 20,
                    opacity: 0.9,
                    marginRight: 14
                  }}
                />
                Administrador salvo com sucesso!
              </span>
            }
            action={[
              <IconButton
                key='close'
                aria-label='Close'
                color='inherit'
                onClick={this.handleSnackClose}
              >
                <CloseIcon style={{ fontSize: 20 }} />
              </IconButton>
            ]}
          />
        </Snackbar>
        <Menu
          id='lock-menu'
          anchorEl={this.state.anchorEl}
          open={Boolean(this.state.anchorEl)}
          onClose={this.handleMenuClose}
        >
          <MenuItem onClick={this.handleDialogOpen}> Editar administrador </MenuItem>
          <MenuItem onClick={() => { this.handleMenuClose(); }}> Excluir administrador </MenuItem>
        </Menu>
      </Root>
    )
  }
}

export default Curriculum
